import 'styles.css';
import Bar from 'components/stickyBar.js';
import Helmet from 'react-helmet';
import Link from 'next/link';
import Dropdown from 'components/dropdown.js';
import Model from '../public/model';

class Gallery extends React.Component{

    constructor(props){
        super(props);
        this.items = ['Logo_light','Logo_dark','Gift'];
    }

    componentDidMount(){
        const models = this.items.map(ele=>
            new Model(ele+'.gltf',false));

        let index = 0;
        console.log(this);
        for(let modelName of this.items){
            console.log(modelName,this[modelName]);
            this[modelName].addChild(models[index]);
            // models[index++].load();
        }

    }

    render(){

    //    const models = this.items.map(ele=>
    //         <Dropdown key={ele} title={ele}><div ref={ref=>(this[ele] = ref)}/><p>!</p></Dropdown>
    //     );
        this.models = this.items.map(ele=>
            <Dropdown key={ele} ref={ref=>(this[ele] = ref)}/>
        );

        // console.log(containers);
        // console.log(containers[0].appendChild);
        return <>
        <Bar>
            <Helmet>
                <title>Gallery</title>
            </Helmet>
            {this.models}
        </Bar>
        </>
    }
}

export default Gallery;